import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import { Alert } from "@mui/material";

const RegisterTextFild = ({
  id,
  label,
  autoComplete,
  onChange,
  errorsState,
  value,
  required,
}) => {
  return (
    <Grid item xs={12}>
      <TextField
        required={required}
        fullWidth
        id={id}
        label={label}
        name={id}
        autoComplete={autoComplete}
        value={value}
        onChange={onChange}
      />
      {errorsState && errorsState[id] && (
        <Alert severity="warning">{errorsState[id]}</Alert>
      )}
    </Grid>
  );
};
export default RegisterTextFild;
