import TextField from "@mui/material/TextField";

const EditeCardTextFilds = ({ id, lable, onchange, value, required }) => {
  return (
    <TextField
      id={id}
      label={lable}
      variant="outlined"
      sx={{ mt: "10px" }}
      onChange={onchange}
      value={value}
      required={required}
    />
  );
};
export default EditeCardTextFilds;
