import { useEffect, useMemo, useState } from "react";
import { Container, Grid, Typography } from "@mui/material";
import CardComponent from "../../components/CardComponent";
import { useNavigate } from "react-router-dom";
import ROUTES from "../../routes/ROUTES";
import axios from "axios";
import homePageNormalization from "./homePageNormalization";
import { useSelector } from "react-redux";
import useQueryParams from "../../hooks/useQueryParams";
import { toast } from "react-toastify";
import PagesComponent from "../../components/pagination/PagesComponent";

let initialDataFromServer = [];

const HomePage = () => {
  const [dataFromServer, setDataFromServer] = useState([]);
  const navigate = useNavigate();
  const userData = useSelector((bigPie) => bigPie.authSlice.userData);
  const query = useQueryParams();
  useEffect(() => {
    axios
      .get("/cards")
      .then(({ data }) => {
        if (userData) data = homePageNormalization(data, userData._id);

        setDataFromServer(data);
      })
      .catch((err) => {});
  }, []);
  const { tempArr: arrToDisplay, arrLenght } = useMemo(() => {
    if (dataFromServer) {
      if (!dataFromServer.length) {
        return { tempArr: [], arrLenght: 0 };
      }
    }
    const filter = query.filter ? query.filter : "";

    let tempArr = dataFromServer.filter((card) =>
      card.title.startsWith(filter)
    );
    let arrLenght = tempArr.length;
    let cardsToSkip = query.page ? query.page : 1;
    cardsToSkip = (cardsToSkip - 1) * 8;
    tempArr = tempArr.slice(cardsToSkip, cardsToSkip + 8);
    return { tempArr, arrLenght };
  }, [query, dataFromServer]);

  const totalPages = Math.ceil(arrLenght / 8);
  const currentPage = query.page ? query.page : 1;
  const handleDeleteCard = (_id) => {
    setDataFromServer((dataFromServerCopy) =>
      dataFromServerCopy.filter((card) => card._id != _id)
    );
  };
  const handleEditCard = (_id) => {
    const found = dataFromServer.find(
      (cardItem) => cardItem._id === _id && cardItem.user_id === userData._id
    );
    if (found) {
      navigate(`${ROUTES.EDITCARD}/${_id}`);
    } else {
      toast.error("Only the card creator can edit the card!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
      });
    }
  };
  const handleLikeCard = async (_id) => {
    try {
      let { data } = await axios.patch("/cards/" + _id);
      setDataFromServer((corent) => {
        let card = corent.find((cardItem) => cardItem._id === _id);
        if (card) {
          card.likes = !card.likes;
        }
        return [...corent];
      });
    } catch (err) {}
  };

  return (
    <Container>
      <Typography variant="h1">Cards Page</Typography>
      <Typography variant="h5" sx={{ marginBottom: "3%" }}>
        On this page you will find business cards of all kinds
      </Typography>
      <Grid container spacing={2}>
        {arrToDisplay.map((card) => (
          <Grid item key={card._id} xs={12} sm={6} md={4} lg={3}>
            <CardComponent
              _id={card._id}
              title={card.title}
              subTitle={card.subtitle}
              phone={card.phone}
              address={`${card.address.city}, ${card.address.street} ${card.address.houseNumber}`}
              img={card.image.url}
              alt={card.image.alt}
              like={card.likes}
              cardNumber={card.cardNumber}
              onDeleteCard={handleDeleteCard}
              onEditCard={handleEditCard}
              onLikeCard={handleLikeCard}
            />
          </Grid>
        ))}
      </Grid>
      <PagesComponent totalPages={totalPages} page={currentPage} />
    </Container>
  );
};

export default HomePage;
